.conference-slider {
  background-color: palegoldenrod;
  padding: 40px 20px;
}
.conference-slider header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.conference-slider header h2 {
  font-size: 3rem;
  margin: 10px auto;
}
.conference-slider header p {
  font-size: 2rem;
  margin: 10px auto;
}
.conference-slider div .flexslider .slides {
  min-height: 512px;
  max-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  list-style: none;
}